form.main-contact-form label {
  display: none; }

form.main-contact-form #edit-top-block {
  position: relative;
  left: 45px;
  width: 600px;
  margin: auto;
  margin-top: 0;
  margin-bottom: 50px; }
  form.main-contact-form #edit-top-block .form-type-textfield, form.main-contact-form #edit-top-block .form-type-date-popup {
    display: inline-block;
    vertical-align: top;
    width: 250px;
    height: 30px; }
  form.main-contact-form #edit-top-block .form-type-textarea {
    width: 530px; }
  form.main-contact-form #edit-top-block .container-inline-date {
    display: inline-block; }
  form.main-contact-form #edit-top-block .form-item:nth-of-type(2n+1) {
